import React from "react";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import AccessTimeFilledIcon from '@material-ui/icons/AccessTimeFilled';
import EditLocationAltIcon from '@material-ui/icons/EditLocationAlt';
import Button from "@material-ui/core/Button";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Box from "@material-ui/core/Box";
import PropTypes from 'prop-types';
import FastfoodIcon from '@material-ui/icons/Fastfood';
import EmojiFoodBeverageIcon from '@material-ui/icons/EmojiFoodBeverage';
import LibraryMusicIcon from '@material-ui/icons/LibraryMusic';
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Divider from "@material-ui/core/Divider";
import ListItemText from '@material-ui/core/ListItemText';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`vertical-tabpanel-${index}`}
            aria-labelledby={`vertical-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
};

export default class HelloComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            tabValue: 0
        };

        this.handleChange = this.handleChange.bind(this);

    }
    handleChange(event, newValue){
        this.setState({
            tabValue: newValue
        })
    }

    render() {
        return <div>
            <Container style={{color: "#564a3a", backgroundColor: "#ffebd4"}}>
                <Grid container spacing={2}
                      justifyContent="center" style={{marginBottom: "5px"}}>
                    <Grid item xs={12} md={10} lg={8}>
                        <Typography variant="h3"  >
                            Hey there!
                        </Typography>
                        <Typography variant="h5"   >
                            You are invited to a BBQ get-together!
                        </Typography>
                    </Grid>
                    <Grid item xs={12} md={10} lg={8}>
                        <div className="customPaper bbgPic">

                        </div>
                    </Grid>
                </Grid>
                <Grid container spacing={2}
                      justifyContent="center" style={{marginBottom: "5px"}}>
                    <Grid item xs={12} md={10} lg={8}>
                        <Typography variant="h3"  style={{textAlign: "left"}} >
                            What is gonna be there?
                        </Typography>
                    </Grid>
                    <Grid item xs={12} md={10} lg={8}>
                        <div className="customPaper">
                            <Grid container>
                                <Grid item xs={3}>
                                    <Tabs orientation="vertical" value={this.state.tabValue} onChange={this.handleChange} aria-label="icon tabs example">
                                        <Tab icon={<FastfoodIcon />}  label="Food"aria-label="phone" />
                                        <Tab icon={<EmojiFoodBeverageIcon />} label="Drinks" aria-label="favorite" />
                                        <Tab icon={<LibraryMusicIcon />} label="Fun" aria-label="person" />
                                    </Tabs>
                                </Grid>
                                <Grid item xs={9}>
                                    <TabPanel value={this.state.tabValue} index={0}>
                                        <Typography variant="body1" gutterBottom>For this BBQ we are going to grill some vegetables (such as eggplants, potatoes, asparagus) with chicken skewers, make a quinoa salad and try vegan burgers! </Typography>
                                         <Typography variant="overline" gutterBottom> CAUTION: IF YOU HAVE DIETARY RESTRICTIONS PLEASE LET US KNOW EARLY </Typography>

                                    </TabPanel>
                                    <TabPanel value={this.state.tabValue} index={1}>
                                        <Typography variant="body1" gutterBottom> Homemade lemonade, water, coffee, tea!</Typography>
                                        <Typography variant="overline" gutterBottom> Alcohol drinks: BYOB :) </Typography>
                                    </TabPanel>
                                    <TabPanel value={this.state.tabValue} index={2}>
                                        <Typography variant="body1" gutterBottom> Music, tabletop games (UNO, Unstable Unicorns), chill conversations will be part of the BBQ!</Typography>

                                    </TabPanel>
                                </Grid>
                            </Grid>

                        </div>

                    </Grid>
                </Grid>

                <Grid container spacing={2}
                justifyContent="center" style={{marginBottom: "5px"}}>
                    <Grid item>

                    </Grid>
                    <Grid item xs={12} md={10} lg={8}>
                        <Typography variant="h3" component="div" style={{textAlign: "left"}} >
                            Where and when?
                        </Typography>
                    </Grid>
                    <Grid item>

                    </Grid>
                    <Grid item xs={12} md={5} lg={4}>
                        <div className="customPaper">
                            <EditLocationAltIcon sx={{ fontSize: 60,  color: "#544937" }} />
                            <Typography variant="h5" component="div">
                                57 Widdicombe Hill Blvd, Etobicoke, ON M9R 1Y4
                            </Typography>
                            <Button style={{marginTop: 10}} variant="outlined" href="https://www.google.com/maps/place/57+Widdicombe+Hill+Blvd,+Etobicoke,+ON+M9R+1Y4">
                                View in Google Maps
                            </Button>
                        </div>
                    </Grid>
                    <Grid item xs={12} md={5} lg={4}>
                    <div className="customPaper">
                        <AccessTimeFilledIcon sx={{ fontSize: 60,  color: "#544937" }} />
                        <Typography variant="h4" component="div">
                            5:00pm
                        </Typography>
                        <Typography variant="h5" component="div">
                            Sunday, August 15th
                        </Typography>
                    </div>
                </Grid>
                </Grid>
                <Grid container spacing={2}
                      justifyContent="center" style={{marginBottom: "5px"}}>
                    <Grid item xs={12} md={10} lg={8}>
                        <Typography variant="h3" component="div" style={{textAlign: "left"}} >
                            More info
                        </Typography>
                    </Grid>
                    <Grid item xs={12} md={10} lg={8}>
                        <div className="customPaper" style={{padding: "10px 25px"}}>
                            <Typography variant="h5" component="div" style={{textAlign: "left"}} >
                                Nice, what do I need to bring?
                            </Typography>
                            <Typography variant="body1" component="div" style={{textAlign: "left"}} >
                               Just you. But you are always welcomed to have extra food / games with you, the more the merrier!
                            </Typography>
                            <Divider/>
                            <Typography variant="h5" component="div" style={{textAlign: "left"}} >
                                Is there seating?
                            </Typography>
                            <Typography variant="body1" component="div" style={{textAlign: "left"}} >
                                Kinda. The amenity includes a table with some chairs, but probably we will be moving around more.
                            </Typography>
                            <Divider/>
                            <Typography variant="h5" component="div" style={{textAlign: "left"}} >
                                More questions?
                            </Typography>
                            <Typography variant="body1" component="div" style={{textAlign: "left"}} >
                                Be free to ask!
                            </Typography>

                        </div>
                    </Grid>
                </Grid>

            </Container>
        </div>;
    }
}
